animate("shake-down", document.querySelector("button"));

document.querySelector("#toggle_dark_mode").addEventListener("click", () => {
  toggleDarkMode();
  animate("push-out", document.querySelector("button"));
  document.querySelector("#toggle_dark_mode").blur();
});

function toggleDarkMode() {
  const theme = document.documentElement.getAttribute("data-theme");
  document.documentElement.setAttribute(
    "data-theme",
    theme === "dark" ? "light" : "dark"
  );
}

function animate(animationClassName, elements, length = 1000) {
  if (!elements.length) {
    elements = [elements];
  }
  elements.forEach((element) => {
    element.classList.add(animationClassName);
    setTimeout(() => {
      element.classList.remove(animationClassName);
    }, length);
  });
}
